<template>
  <div class="mymain">
    <el-container style="height: 100%; border: 1px solid #eee">
      <el-header>
        <nav-top></nav-top>
      </el-header>
      <el-container>
        <el-aside style="background-color: rgb(245, 245, 245);border-right: 1px solid #d9d9d9;">
          <nav-left></nav-left>
        </el-aside>
        <el-main>
          <separator></separator>
          <div class="router-view">
            <router-view style="padding:15px;"></router-view>
          </div>
        </el-main>
      </el-container>
    </el-container>

  </div>
</template>
<script>
  import navLeft from './navMgt/navLeft'
  import navTop from './navMgt/navTop'
  import separator from './navMgt/separator'
  export default {
    name: 'mymain',
    components: {navLeft, navTop, separator}
  }
</script>
<style lang="scss" type="text/css">
.el-header {
  height: 50px !important;
  line-height: 50px;
  padding-left: 20px;
  padding-right: 20px;
  background-color: #fff;
  border-bottom: 1px solid #d9d9d9;
}
img {
  width: 100px;
  float: left;
  padding-top: 9px;
}
.el-main {
  padding: 0;
  position: relative;
}
.router-view{
  background-color: #fff;
  position: absolute;
  left: 0;
  top: 41px;
  bottom: 0;
  width: 100%;
}
.el-container.is-vertical {
  border: none !important;
}
.mymain{
  height: 100%;
}
</style>





