<template>
  <div class="navTop">
    <img src='https://js.tuandai.com/images/default/logo.png?v=20170808001' />
    <div class="user-info">
      <el-dropdown>
        <div class="el-dropdown-link" placement="top">
          <div class="user-icon">
            <img src="http://www.cliptheme.com/preview/cliponeV2/Admin/clip-one-template/clip-one/assets/images/avatar-1-small.jpg" class="user-img"/>
          </div>
          lady<i class="el-icon-arrow-down el-icon--right"></i>
        </div>
        <el-dropdown-menu slot="dropdown" placement="top">
          <el-dropdown-item>
            <div @click="changePwd = true"><i class="el-icon-edit-outline"></i> 修改密码</div>
          </el-dropdown-item>
          <el-dropdown-item>
            <div @click="logoutView = true"><i class="el-icon-circle-close"></i> 登出</div>
          </el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
    </div>
    <el-dialog title="退出登录" :visible.sync="logoutView">
      <p>确定要退出登录吗?</p>
      <span slot="footer" class="dialog-footer">
        <el-button @click="logoutView = false">取 消</el-button>
        <el-button type="primary" @click="logoutFun()">确 定</el-button>
      </span>
    </el-dialog> 

    <el-dialog title="修改密码" :visible.sync="changePwd">
      <el-form :model="ruleForm2" status-icon :rules="rules2" ref="ruleForm2" label-width="100px" class="demo-ruleForm">
        <el-form-item label="原密码" prop="oldPwd">
          <el-input v-model="ruleForm2.oldPwd"></el-input>
        </el-form-item>
        <el-form-item label="密码" prop="pass">
          <el-input type="password" v-model="ruleForm2.pass" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="确认密码" prop="checkPass">
          <el-input type="password" v-model="ruleForm2.checkPass" auto-complete="off"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="changePwd = false">取 消</el-button>
        <el-button type="primary" @click="submitForm('ruleForm2')">确 定</el-button>
      </span>
    </el-dialog> 

  </div>
</template>

<script>
  import '../../assets/css/navTop.css'
  export default {
    data () {
      var checkPwd = (rule, value, callback) => {
        if (!value) {
          return callback(new Error('原密码不能为空'))
        }
        setTimeout(() => {
          if (!Number.isInteger(value)) {
            callback(new Error('请输入数字值'))
          } else {
            if (value < 18) {
              callback(new Error('必须年满18岁'))
            } else {
              callback()
            }
          }
        }, 1000)
      }
      var validatePass = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请输入密码'))
        } else {
          if (this.ruleForm2.checkPass !== '') {
            this.$refs.ruleForm2.validateField('checkPass')
          }
          callback()
        }
      }
      var validatePass2 = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请再次输入密码'))
        } else if (value !== this.ruleForm2.pass) {
          callback(new Error('两次输入密码不一致!'))
        } else {
          callback()
        }
      }
      return {
        ruleForm2: {
          pass: '',
          checkPass: '',
          oldPwd: ''
        },
        rules2: {
          pass: [
            { validator: validatePass, trigger: 'blur' }
          ],
          checkPass: [
            { validator: validatePass2, trigger: 'blur' }
          ],
          oldPwd: [
            { validator: checkPwd, trigger: 'blur' }
          ]
        },
        logoutView: false,
        changePwd: false
      }
    },
    methods: {
      logoutFun () {
        sessionStorage.clear()
        this.$router.push({ name: 'login' })
      },
      submitForm (formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            alert('submit!')
          } else {
            console.log('error submit!!')
            return false
          }
        })
      }
    }
  }
</script>
<style lang="scss" scoped type="text/css">

</style>




