<template>
  <div>
    <el-menu default-active="1-4-1" class="el-menu-vertical-demo" @open="handleOpen" @close="handleClose" :collapse="isCollapse" router background-color="#f5f5f5" active-text-color="#007AFF">
      <div class="shrink" @click="isCollapse=!isCollapse">
        <div class="shrink-block">
          <span v-if="isCollapse">展开</span>
          <span v-else>收起</span>
        </div>
      </div>

      <el-submenu v-for="(ml, key) in menuList" :key="ml.resId" v-bind:index="(key+1).toString()">
        <template slot="title">
          <i class="el-icon-location"></i>
          <span slot="title">{{ml.name}}</span>
        </template>
        <el-menu-item-group v-for="ml2 in ml.childList" :key="ml2.resId">
          <el-menu-item v-bind:index="ml2.url">{{ml2.name}}</el-menu-item>
        </el-menu-item-group>
      </el-submenu>

      <el-menu-item index="/">
        <i class="el-icon-setting"></i>
        <span slot="title">首页</span>
      </el-menu-item>
      <el-menu-item index="form">
        <i class="el-icon-setting"></i>
        <span slot="title">form</span>
      </el-menu-item>
      <el-menu-item index="table">
        <i class="el-icon-setting"></i>
        <span slot="title">table</span>
      </el-menu-item>
      <el-menu-item index="transfer">
        <i class="el-icon-setting"></i>
        <span slot="title">transfer</span>
      </el-menu-item>
      <el-menu-item index="echart">
        <i class="el-icon-setting"></i>
        <span slot="title">echart</span>
      </el-menu-item>

    </el-menu>
  </div>
</template>
<script>
  import jQuery from 'jquery'
  import '../../assets/css/navLeft.css'
  export default {
    data () {
      return {
        isCollapse: false,
        menuList: []
      }
    },
    mounted () {
      let that = this
      // 屏幕变化，菜单收起
      jQuery(window).resize(function () {
        let winWidth = jQuery(window).width()
        if (winWidth < 768) {
          that.isCollapse = true
        } else {
          that.isCollapse = false
        }
      })
      // 菜单数据
      this.$api.promise({
        method: 'POST',
        url: 'menu',
        data: {'name': '12345'}
      })
      .then(res => {
        if (res.data.code > 0) {
          this.menuList = res.data.list.filter(ls => {
            return ls.parentResId === -1
          })
          this.menuList.forEach((val, key) => {
            res.data.list.forEach((val2, key2) => {
              if (val2.parentResId === val.resId) {
                if (!Array.isArray(val.childList)) {
                  val.childList = []
                }
                val.childList.push(val2)
              }
            })
          })
          console.dir(this.menuList)
        } else {
          this.$message.error(res.data.desc)
        }
      })
      .catch((err) => {
        // this.fullscreenLoading = false
        this.$message.error('接口异常：' + err)
      })
    },
    methods: {
      handleOpen (key, keyPath) {
        console.log(key, keyPath)
      },
      handleClose (key, keyPath) {
        console.log(key, keyPath)
      }
    }
  }
</script>

<style>
.el-menu-item-group__title{
  display: none;
}
</style>





